export interface WinningNumbers {
  period: string; // e.g., "112 09-10"
  specialPrize: string; //  (1000) - 8 digits
  grandPrize: string; //  (200) - 8 digits
  firstPrize: string[]; //  (20) - Array of 8 digits strings
  additionalSixthPrize: string[]; //  (200) - Array of 3 digits strings
}

export enum PrizeType {
  Special = ' (1,000)',
  Grand = ' (200)',
  First = ' (20)',
  Second = ' (4)',
  Third = ' (1)',
  Fourth = ' (4,000)',
  Fifth = ' (1,000)',
  Sixth = ' (200)',
  None = '',
  Error = '',
}

export interface CheckResult {
  isWinner: boolean;
  prizeType: PrizeType;
  matchedDigits: number; // How many digits matched at the end
  description: string;
}

export interface ReceiptScanResult {
  number: string;
  period?: string;
}

export interface WinningRecord {
  id: number;
  date: string;
  period: string;
  number: string;
  prizeType: string;
  amount: string;
}

export interface PendingReceipt {
  id: number;
  number: string; // 3 digits
  period: string;
  dateAdded: string;
}